* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: beige;
  color: #000000;
  font-family: Roboto, sans-serif;
}

header {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  margin-top: 10px;
}

h1 {
  letter-spacing: 2px;
  margin: 20px;
  text-align: center;
}

button,
input[type="submit"] {
  background: #3f5efb;
  border: none;
  border-radius: 5px;
  color: #fff;
  font-size: large;
  letter-spacing: 1px;
  padding: 0.5rem 1rem;
}

button:hover,
input[type="submit"]:hover {
  background: rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

input[type="submit"] {
  width: 90%;
}

input[type="text"] {
  padding: 10px;
  width: 90%;
}

.card,
.routingBtn,
.sortContainer,
form,
main {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
}

form {
  width: 20%;
}

.card,
form {
  flex-direction: column;
}

.card {
  border: 1px solid black;
  border-radius: 5px;
  box-shadow: 5px 5px rgba(0, 0, 0, 0.2);
  padding: 10px;
  position: relative;
  width: 350px;
}

main {
  margin: 20px;
}

.heartIconCard {
  cursor: pointer;
  left: 10px;
  position: absolute;
  top: 10px;
}

.overview {
  -webkit-line-clamp: 4;
  line-clamp: 4;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.title {
  font-size: 1.5rem;
  font-weight: 700;
  height: 50px;
  margin-bottom: 10px;
  text-align: center;
}
